.letter-container {
  height: 100%;
}
.valentines-day-card {
  position: relative;
  width: 300px;
  height: 400px;
  background-color: #ff8fa3;
  overflow: hidden;
  border: 5px solid white;
  border-radius: 10px;
}
.valentines-day-card:before {
  content: "";
  position: absolute;
  background-color: #ffb3c1;
  width: 90px;
  height: 90px;
  border-radius: 50%;
  top: 240px;
  left: -25px;
  box-shadow: 60px 20px #ffb3c1, 130px 10px #ffb3c1, 190px 20px #ffb3c1, 260px -5px #ffb3c1, 340px 20px #ffb3c1;
}
.valentines-day-card:after {
  content: "";
  position: absolute;
  background-color: #ffccd5;
  width: 90px;
  height: 90px;
  border-radius: 50%;
  top: 260px;
  left: -40px;
  box-shadow: 50px 20px #ffccd5, 120px 40px #ffccd5, 200px 15px #ffccd5, 260px 30px #ffccd5, 330px 30px #ffccd5,380px 10px #ffccd5;
}
.clouds {
  position: absolute;
  width: 90px;
  height: 90px;
  border-radius: 50%;
  background-color: #fff0f3;
  z-index: 5;
  top: 310px;
  left: -20px;
  box-shadow: 55px 30px #fff0f3, 125px 10px #fff0f3, 175px 20px #fff0f3, 250px 10px #fff0f3, 320px 5px #fff0f3, 380px 10px #fff0f3;
}

/* **************************************** */

.hearts {
  position: absolute;
  z-index: 2;
}
.heartOne {
  position: absolute;
  left: 12px;
  top: 400px;
  animation: upTwo 11s linear infinite 1s;
}
.heartTwo {
  position: absolute;
  left: 240px;
  top: 400px;
  animation: upTwo 9s linear infinite 2s;
}
.heartThree {
  position: absolute;
  left: 85px;
  top: 400px;
  animation: upTwo 13s linear infinite 3s;
}
.heartFour {
  position: absolute;
  left: 280px;
  top: 400px;
  animation: upTwo 9s linear infinite 4s;
}
.heartFive {
  position: absolute;
  left: 150px;
  top: 400px;
  animation: upTwo 13s linear infinite 4.5s;
}
.right-side:before, .right-side:after {
  position: absolute;
  content:"";
  transform-origin: left;
  animation: move 1s ease infinite;
}
.right-side:before {
  border-radius:50% 50% 50% 0;
  width:21px;
  height:20px;
  background-color: #c9184a;
  left:-0.26px;
}
.right-side:after {
  height:0;
  width:0;
  border-bottom:10px solid transparent;
  border-left: 15px solid #c9184a;
  top:19px;
  left:-0.26px;
}
@keyframes move {
  0%, 100% {transform: rotateY(0);}
  50% {transform: rotateY(-45deg);}
}

.left-side:before, .left-side:after {
  position: absolute;
  content:"";
  transform-origin: right;
  animation: move 1s ease infinite;
}
.left-side:before {
  border-radius:50% 50% 0 50%;
  width:20px;
  height:20px;
  left:-20.7px;
  background-color: #ff4d6d;
}
.left-side:after {
  height:0;
  width:0;
  left:-15px;
  border-bottom:10px solid transparent;
  border-right: 15px solid #ff4d6d;
  top:19px;
}
@keyframes up {
  0% {transform: translateY(0);}
  100% {transform: translateY(-200px);}
}
@keyframes upTwo {
  0% {transform: translateY(0);}
  100% {transform: translateY(-450px);}
}

/* **************************************** */

.text {
  position: absolute;
  line-height: 1;
  text-align: center;
  width: 290px;
  z-index: 3;
  top: 370px;
  animation: up 2s ease forwards;
  transition: .2s;
  color: var(--main-text);
  font-size: 2.5rem;
  font-family: 'Pacifico', cursive;
}
.text span {
  color: var(--main-text);
  font-size: 2.5rem;
  font-family: 'Pacifico', cursive;
}
.hover {
  color: var(--main-text);
  font-family: 'Pacifico', cursive;
  text-align: center;
  font-size: 1.5rem;
  margin-top: 10px;
}
.text:hover span {
  display: none;
}
.text:hover:before {
  content:"Te amo mi Jahaira";
  font-size:25px;
}